nav{
    display: flex;
    border-bottom: 2px solid #f3f3f3;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
    z-index: 999;
    margin-left: 2rem;
    min-width: 600px;
}

input{
    padding: 12px 20px;
    border: none;
    background: #f7f6f6;
    outline: none;
    margin-right: 20px;
    border-radius: 5px;
    position: relative;
    left: 150px;
    width: 14rem;
}

.nav-icon{
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 2rem;

}